<template>
  <div id="maintop">
    <div  v-if="flag">
    <b-input
      class="mb-2 mr-sm-2 mb-sm-0 title"
      placeholder="" v-model="name"
    ></b-input>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 title"
              placeholder="" v-model="bonus"
      ></b-input>
      <strong style="color: rgba(240,56,6,0.75)"> {{ bonus/2 }} </strong>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 content"
              placeholder=""  v-model="contact"
      ></b-input>

      
    <div id="main">
          <mavon-editor v-model="content" ref="md" @imgAdd="uploadimg" style="height: 100%"/>
    </div>

      <br>
      
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 hour"
              placeholder="" v-model="hour"
      ></b-input>
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 hour"
              placeholder="" v-model="minute"
      ></b-input>
      <br>


    <div class="btn">
      <b-button variant="outline-primary" @click="post"></b-button>
    </div>


      <!--<button @click="get"></button>-->
    </div>
    <!---->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "PostOrder",
      data() {
        return {
          db:{},
          content: '',
          name: '',
          contact: '',
          bonus: '',
          hour: '',
          minute: '',
          isLoading: false,
          flag: false
        }
      },

      methods: {
        post(){
            this.flag = false
            this.isLoading = true
            let nickname = this.$store.state.nickname
            let instance = this.$store.state.bountyinstance
            let addr = this.$store.state.addr
            let name= this.name
            let taskNumber = this.getNum()
            let bonus= this.bonus
            let contact = this.contact
            let content = this.content
            let hour = this.hour
            let minute = this.minute
            let created = this.getTimeStamp()
            let _this = this

            async function main(){
              let value = _this.web3.utils.toWei(bonus, 'ether')
              // console.log(699, bonus)

              let task = {
                "name": name,
                "nickname": nickname,
                "taskNumber": taskNumber,
                "contact": contact,
                "content": content,
                'bonus': value,
                'created': created
              }

              //ipfs
              let con = Buffer.from(JSON.stringify(task))
              let res = await _this.ipfs.add(con)
              let hash = res[0].hash

              let s = await instance.setTask(hash, value, hour, minute, {from: addr})
              // console.log(555, s)

              //vuex
              _this.$store.commit('saveBountys', [])
              _this.$router.push({path:'/bounty'})
            }
            main()
          },
        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        }
      },

      components: {
        Loading
      },

      mounted() {//
        let that = this
        let instance = this.$store.state.bountyinstance
        let addr = this.$store.state.addr
        // console.log(32221, instance)

        async function main(){
          if(that.$store.state.nickname){
            that.flag = true
            //
            if(Object.keys(instance).length === 0){
              //
              // console.log(777777, "instance")
              let res = await that.axios.get('Bounty.json')
              // console.log(667, res.data)
              let contract = that.TruffleContract(res.data)
              // console.log(77, contract)
              contract.setProvider(that.web3.currentProvider)
              let instance = await contract.deployed()
              // console.log(31111, instance)
              //vuex
              that.$store.commit('saveBountyInstance', instance)
              that.flag = true
            }else{
              //instance
              // console.log(88888, "instance")
              that.flag = true
            }

          }
        }
        main()
      }
    }
</script>

<style scoped>
  #maintop{
    width: 90%;
    height: 100%;
    margin: 0 auto;
  }

  #main{
    height: 30rem;
  }
  .title{
    margin-top: 1.5rem;
    margin-bottom: 1.5rem !important;
    width: 60%;
  }
  .content{
    margin-top: 1.5rem;
    margin-bottom: 1.5rem !important;
    width: 60%;
    background-color: bisque;
  }
  .hour{
    display: inline;
    width: 20%;
  }
  .btn{
    margin-top: 0.3rem;
  }
