<template>
  <div class="article">
     <div class="content" >

        <p class="posttime"><span style="margin-right: 1.5rem;margin-left: 2rem">{{ order.orderNumber }} </span>
            {{ order.created | changeTime}}</p>

        <p class="title">{{ order.name }}</p>

         <div class="text">
             @{{  order.nickname }}({{ order.addr }})<br>
             {{ order.buyers.length }} <br>
              {{ weiToEth(order.price) }}  <br>
            <p v-if="isPurchased" >
                 <span class="neirong">{{ order.content }}</span><br>
                <span class="neirong"> {{ order.contact }} </span>
            </p>
             <p v-else style="display: inline;">
                  <span class="neirong2"></span><br>
             </p>
             <!-- {{ order[6] }}<br>-->
             
             <mavon-editor
                     :value="order.body + '<br>'"
                     :subfield = "prop.subfield"
                     :defaultOpen = "prop.defaultOpen"
                     :toolbarsFlag = "prop.toolbarsFlag"
                     :editable="prop.editable"
                     :scrollStyle="prop.scrollStyle"
                     :boxShadow=false
             ></mavon-editor>
         </div>

         <div v-if="!isMyself">
             <div v-if="isPurchased" class="btn2"></div>
             <div class="btn" v-else>
                 <b-button variant="outline-primary" @click="buy(order.uid)"></b-button>
             </div>
         </div>

    </div>

      <!---->
      <transition name="fade">
          <loading v-if="isLoading"></loading>
      </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "Order",
      data(){
          return{
            order: [],
            keywords:'',
            author:'',
            permlink:'',
            isLoading: false,
            isMyself: false,
            isPurchased: false
          }
      },
      methods:{
          buy(id){
              let _this = this
              let instance = this.$store.state.ordertinstance
              let addr = this.$store.state.addr
              async function main() {
                let s = await instance.confirmPurchase(id, {from: addr})
                console.log(555, s)
                _this.isPurchased = true
                  //vuex
                 _this.$store.commit('saveOrders', [])
              }
              main()
        },
          getOrderDone(){
              let orders = this.$store.state.orders
              let addr = this.$store.state.addr
              orders.forEach(item => {
                  if(item.orderNumber === this.$route.params.orderNumber){
                      this.order = item
                      // console.log(622, '')
                  }
              })

              //
              if(this.order.addr.toLowerCase() === addr){
                  this.isMyself = true
                  console.log(88899, "")
              }

              //
              let buyers = this.order.buyers
              buyers.forEach(item => {
                  if(item.toLowerCase() == addr){
                      console.log(566, "")
                      this.isPurchased = true
                  }
              })
          }
      },


      components: {
        Loading
      },
      computed: {
            prop () {
                let data = {
                    subfield: false,
                    defaultOpen: 'preview',
                    editable: false,
                    toolbarsFlag: false,
                    scrollStyle: true
                }
                return data
            },

        },

        mounted() {
            let that = this
            let instance = this.$store.state.ordertinstance
            async  function main(){
                if(Object.keys(instance).length === 0){
                    //, instance
                    console.log(868, "instance")
                    instance = await that.getOrderInstance()
                    await that.getOrders(instance)
                    that.getOrderDone()
                    that.isLoading = false
                } else{
                    if(that.$store.state.orders == false){
                        console.log(667, "")
                        //
                        await that.getOrders(instance)
                        that.getOrderDone()
                        that.isLoading = false
                    } else{
                        console.log(2233, "")
                        that.getOrderDone()
                        that.isLoading = false
                    }
                }
            }
            main()
        },

      filters:{
        changeTime(timestamp){
              //  yyyy-MM-dd hh:mm:ss
              let date = new Date(timestamp * 1000)
              let Y = date.getFullYear() + '-'
              let  M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-'
              let D = date.getDate() + ' '
              let h = date.getHours() + ':'
              let m = date.getMinutes() + ':'
              let s = date.getSeconds()
              return Y+M+D+h+m+s
          },
      },
    }
</script>

